
body {
  margin: 0;
  background-color:#F9FAFB;
  font-family: 'Roboto', sans-serif;
}



@keyframes RtoL {
  0% {
    opacity: 0;
    right: 100%;
  }
  100% {
    right: -2%;
    opacity: .2;
  }
}

.RtoL {
  animation: linear 0.5s normal RtoL;
}


@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.fadein {
  animation: linear 0.1s normal fadein;
}


@-webkit-keyframes beat {
  0% {
      -webkit-transform: scale(1);
      transform: scale(1);
  }
  100% {
      -webkit-transform: scale(1.5);
      transform: scale(1.5);
  }
}

@keyframes beat {
  0% {
      -webkit-transform: scale(1);
      transform: scale(1);
  }
  100% {
      -webkit-transform: scale(1.2);
      transform: scale(1.2);
  }
}


.beat {
  animation: beat linear .3s alternate  infinite;
  -webkit-animation: beat linear .3s alternate infinite;
}
